import React from 'react';


const SocialIcons=()=>{
   
    return (
        <ul className='socialIcons'>
            <li><a href='#' style={{color:'purple'}}>Facebook</a><i style={{background:'purple'}} className='fab fa-facebook-f'></i></li>
            <li><a href='#' style={{color:'#ff0066'}}>Instagram</a><i style={{background:'#ff0066'}} className='fab fa-instagram'></i></li>
            <li><a href='#' style={{color:'aqua'}}>Twitter</a><i style={{background:'aqua'}} className='fab fa-twitter'></i></li>
            <li><a href='#' style={{color:'orangered'}}>G-Mail</a><i style={{background:'orangered'}} className='fab fa-google'></i></li>
        </ul>
    );

};

export default SocialIcons;