import React from 'react';
import './style.css';
import Navbar from './Navbar';
import SocialIcons from './SocialIcons';
import HiringInfo from './HiringInfo';
import Objectives from './Objectives';
import {Route,Switch,Link, BrowserRouter} from 'react-router-dom';

const GeneralComponents=()=>{

  return(
  <>
     <Navbar/>
       <section id='home' className='section'>
         <div className='profile--img'>
           <img src='images/nehal.jpg' />
           <span className='line--border'></span>
           <span className='cover--border'></span>
         </div>
          <label >What are you looking for..?</label>
          <h1>Here I'm <span>Nikhil</span></h1>
          <span id='type' data-text='Web Developer And Designer'>Web Developer And Designer</span>
          <SocialIcons/>
          <Link id='hire' to='/hireme' >HireMe</Link>
       </section>
       <section id='about' className='section'>
         <h1>About</h1>
         <div className='about-contents'>
           <div className='image'>
            <div class='image--box'>
            <img src='images/nehal.jpg' />
            </div>
           </div>
           <div className='summary'>
             <h1>Do You Know?</h1>
             <span>Who the hell am I?</span>
             <h2>I am not a superman with red underpant.</h2>
             <p>My name is Nikhil Gupta. 
             I am passionate,friendly and kind type of person and do not like to be involved in politics. 
             I am learning designing and developing website from 2 years.</p>
             <a className='button'>More</a>
           </div>
         </div>
         <Objectives/>
       </section>
  </>
  );
}

const App=()=>{
    return (
      <>
      <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Switch>
        <GeneralComponents/>
       <Route path={process.env.PUBLIC_URL+'/react-deploy/'} exact component={GeneralComponents}/>
       <Route path={process.env.PUBLIC_URL+'/react-deploy/hireme'} exact component={HiringInfo}/>
      </Switch>
      </BrowserRouter>
       </>
    );
}

export default App;
