import React from 'react';
import {Link} from 'react-scroll';
const Menu=(props)=>{
    return (
        <Link
    to={props.to} spy={true} smooth={true} activeClass='active' offset={-40} duration={500} data-text={props.to} >{props.to}</Link>
    );
}
const Navbar=()=>{
  
    const links=[{to:'home'},{to:'about'},{to:'gallery'},{to:'skills'},{to:'contact'}];

    const navbars=links.map((obj,ind)=>{
     return <Menu to={obj.to}/> 
    });

   return(
     <nav className='navbar'>
         <label id='logo'><span>N</span>k<span>S</span>ource<span>G</span>uide</label>
         <ul id='nav--links'>
             {navbars}
        </ul> 
        <div className='toggle--menu'>
            <span></span>
            <span></span>
            <span></span>
        </div>
     </nav>
   );
}

export default Navbar;